<template>
  <div class="upload_main">
    <Row>
      <Col span=24>
        <h1 style="text-align: center;margin-bottom: 20px">XX公司保密文件系统</h1>
      </Col>
    </Row>
    <Row>
      <Col span=24>
        <div class="back" @click="upload"><Icon type="md-cloud-upload" />上传</div>
      </Col>
    </Row>
    <uploader v-if="!showUp" :options="options" :fileStatusText="fileStatusText" class="uploader-example">
      <uploader-unsupport></uploader-unsupport>
      <uploader-drop>
        <p>拖拽文件或者</p>
        <uploader-btn>选择文件</uploader-btn>
      </uploader-drop>
      <uploader-list></uploader-list>
    </uploader>
    <Row>
      <Col span="24">
        <div class="title">全部文件</div>
      </Col>
    </Row>
    <Row  v-for="(item, index) in list" :key="index">
      <Col span="24">
        <div class="document"><Icon type="md-document" style="margin-right: 4px" />{{item}}</div>
      </Col>
    </Row>
  </div>
</template>

<script>
  import { getToken, getList,getUpload } from "@/api/list";
export default {
  name: 'upload',
  data () {
    return {
      list: [],
      options: {
        target: '',
        testChunks: false
      },
      token:'',
      showUp:true
    }
  },
  methods:{
    upload(){
      this.showUp=!this.showUp
    },
    async getUserToken() {
      let res = await getToken();
      this.token=res.data.success
      console.log("this.token",this.token)
      this.options.target='/upload?token='+this.token
    },
    fileStatusText(status) {
      if (status === 'success' ) {
        this.getUserList()
      }
    },
    async getUserList() {
      let res1 = await getToken();
      let params={token:res1.data.success}
      let res = await getList(params);
      this.list=res.data.files
      console.log(res)
      // this.token=res.success
    },
    async userUpload(){
      let params={token:this.token}
      let res = await getUpload()
    }
  },
  created() {

  },
  mounted() {
    this.getUserToken()
    this.getUserList()
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back{
  margin-left: 60px;
  width: 70px;
  height: 40px;
  background-color: #09AAFF;
  justify-content: center;
  align-items: center;
  line-height: 40px;
  color: white;
  border-radius: 6px;
  cursor: pointer;
}
.uploader-example {
  width: 880px;
  padding: 15px;
  margin: 40px auto 0;
  font-size: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .4);
}
.uploader-example .uploader-btn {
  margin-right: 4px;
}
.uploader-example .uploader-list {
  max-height: 440px;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
}
.title{
  margin-top: 16px;
  margin-left: 60px;
  margin-bottom: 8px;
  text-align: left;
  font-size: 13px;
  color: #888;
  border-bottom: 1px solid #f2f6fd;
  margin-right: 60px;
}
.document{
  margin-top: 16px;
  margin-left: 70px;
  margin-bottom: 8px;
  text-align: left;
  font-size: 15px;
  color: #424e67;
  border-bottom: 1px solid #f2f6fd;
  margin-right: 70px;
}
  a{
    color: #424e67!important;
  }
</style>



